.white {
  --primary-color: #121212;
  --secondary-color: #222;
  --tertiary-color: #333;
  --dark-gray-1: #444;
  --dark-gray-2: #555;
  --dark-gray-3: #666;
  --light-gray: #c4c4c4;

  --heading-1: 9.6rem;
  --heading-2: 6rem;
  --heading-3: 4.8rem;
  --heading-4: 3.4rem;
  --heading-5: 2.4rem;
  --heading-6: 2rem;
  --body-text: 1.8rem;
  --button-text: 1.6rem;
  --bg: #fff;
}
.dark {
  --primary-color: #f7f7f7;
  --secondary-color: #eee;
  --tertiary-color: #e5e5e5;
  --dark-gray-2: #bebebe;
  --dark-gray-3: #cacaca;
  --light-gray: #c4c4c4;

  --bg: #121212;

  .project__img {
    filter: brightness(80%);

    &:hover {
      filter: brightness(100%);
    }
  }
}

//1200px / 16
$bp-largest: 75em;
//1100
$bp-large: 68.75em;
